.tree-view-index-model{background:url(/dolphinscheduler/ui/images/dag_bg.png?6a0c3839385c7d50f21acf06989addf4);position:relative}.tree-view-index-model .tree-limit-select{position:absolute;right:20px;top:22px;z-index:1}.tree-view-index-model .tasks-color{min-height:76px;background:#fff;padding-left:20px;position:relative;padding-bottom:10px}.tree-view-index-model .tasks-color .toolbar-color-sp{padding:12px 0}.tree-view-index-model .tree-model{width:100%;height:calc(100vh - 224px);overflow-x:scroll}.tree-view-index-model .d3-tree{padding-left:30px}.tree-view-index-model .d3-tree .node text{font:11px sans-serif;pointer-events:none}.tree-view-index-model .d3-tree rect{cursor:pointer}.tree-view-index-model .d3-tree rect.state{stroke:#666;shape-rendering:crispEdges}.tree-view-index-model .d3-tree path.link{fill:none;stroke:#666;stroke-width:2px}.tree-view-index-model .d3-tree circle{stroke:#666;fill:#0097e0;stroke-width:1.5px;cursor:pointer}